"use client";

import { useEffect, useRef, useState } from "react";
import "leaflet/dist/leaflet.css";

interface Flight {
  id: string;
  agentId: string;
  date: string;
  startTime: string;
  endTime: string;
  durationLabel: string;
  distance: number;
  status: "ACTIVE" | "COMPLETED";
  color: string;
  latlngs?: [number, number][]; 
}

export default function Admin() {
  const mapRef = useRef<any>(null);
  const LRef = useRef<any>(null);
  const layersRef = useRef<Record<string, any>>({}); 

  const [flights, setFlights] = useState<Flight[]>([]);
  const [loading, setLoading] = useState(true);
  const [playing, setPlaying] = useState<string | null>(null);

  useEffect(() => {
    async function init() {
      const LModule = await import("leaflet");
      const L = LModule.default || LModule;
      LRef.current = L;

      // Dark theme map for better visibility of tracking lines
      mapRef.current = L.map("adminMap", { zoomControl: false }).setView([19.0176, 72.8561], 13);
      L.control.zoom({ position: 'bottomright' }).addTo(mapRef.current);

      L.tileLayer("https://{s}.basemaps.cartocdn.com/dark_all/{z}/{x}/{y}{r}.png", {
        attribution: '&copy; CartoDB'
      }).addTo(mapRef.current);

      fetchSummaries();
    }
    init();
  }, []);

  // Generate consistent color based on Agent ID
  function getAgentColor(name: string) {
    const colors = ["#3b82f6", "#10b981", "#f59e0b", "#ef4444", "#8b5cf6", "#ec4899"];
    let hash = 0;
    for (let i = 0; i < name.length; i++) {
      hash = name.charCodeAt(i) + ((hash << 5) - hash);
    }
    return colors[Math.abs(hash) % colors.length];
  }

  async function fetchSummaries() {
    try {
      const res = await fetch("/api/mobilelocation");
      const ids = await res.json();
      
      const summaries: Flight[] = ids.map((id: string) => {
        // Assume ID format like "AGENTID-TIMESTAMP"
        const agentId = id.includes('-') ? id.split('-')[0] : "Agent";
        return {
          id,
          agentId,
          status: "COMPLETED",
          color: getAgentColor(agentId),
          durationLabel: "Click Focus to Load",
          distance: 0,
        };
      });

      setFlights(summaries);
      setLoading(false);
    } catch (e) {
      console.error("Dashboard failed to load", e);
    }
  }

  async function loadPath(id: string) {
    if (layersRef.current[id]) return layersRef.current[id].latlngs;

    const res = await fetch(`/api/mobilelocation?trip_id=${id}`);
    const history = await res.json();
    if (!history.length) return null;

    const L = LRef.current;
    const latlngs: [number, number][] = history.map((p: any) => [p.latitude, p.longitude]);
    const agentId = history[0].agent_id || "Agent";
    const color = getAgentColor(agentId);

    const polyline = L.polyline(latlngs, { color, weight: 3, opacity: 0.8 });
    
    // Cleaner pinpoint marker
    const lastPoint = latlngs[latlngs.length - 1];
    const marker = L.circleMarker(lastPoint, {
      radius: 6,
      fillColor: color,
      color: "#fff",
      weight: 2,
      fillOpacity: 1
    });

    const group = L.featureGroup([polyline, marker]).addTo(mapRef.current);
    layersRef.current[id] = { group, polyline, latlngs };
    
    return latlngs;
  }

  async function focusFlight(id: string) {
    const latlngs = await loadPath(id);
    if (latlngs) {
      mapRef.current.fitBounds(layersRef.current[id].group.getBounds(), { padding: [40, 40] });
    }
  }

  async function replayFlight(id: string) {
    if (playing) return;
    setPlaying(id);
    const latlngs = await loadPath(id);
    if (!latlngs) return;

    const poly = layersRef.current[id].polyline;
    poly.setLatLngs([]);

    for (let i = 0; i < latlngs.length; i++) {
      poly.addLatLng(latlngs[i]);
      if (i % 3 === 0) await new Promise(r => setTimeout(r, 20));
    }
    setPlaying(null);
  }

  return (
    <div className="h-screen bg-[#0b0e14] text-gray-300 font-sans flex flex-col overflow-hidden">
      {/* Top Telemetry Bar */}
      <div className="flex justify-between px-6 py-4 bg-[#161b22] border-b border-gray-800 items-center">
        <h1 className="text-blue-500 font-bold tracking-tighter text-xl">HELICOPTER <span className="text-white">RADAR</span></h1>
        <div className="flex gap-8 text-[10px] uppercase tracking-widest font-bold">
          <div className="flex flex-col"><span className="text-gray-500">Fleet Count</span><span className="text-white text-lg">{flights.length}</span></div>
          <div className="flex flex-col"><span className="text-gray-500">Uptime</span><span className="text-green-500 text-lg">99.9%</span></div>
        </div>
      </div>

      <div className="flex flex-1 overflow-hidden">
        {/* Radar Sidebar */}
        <div className="w-80 bg-[#0b0e14] border-r border-gray-800 overflow-y-auto">
          {loading ? (
            <div className="p-10 text-center animate-pulse text-blue-500 font-bold">BOOTING SYSTEM...</div>
          ) : (
            flights.map(flight => (
              <div key={flight.id} className="p-4 border-b border-gray-800 hover:bg-[#161b22] transition-colors">
                <div className="flex items-center justify-between mb-3">
                  <div className="flex items-center gap-2">
                    <div className="w-2 h-2 rounded-full shadow-[0_0_8px] shadow-current" style={{ color: flight.color, backgroundColor: flight.color }} />
                    <span className="text-xs font-bold text-white uppercase">{flight.agentId}</span>
                  </div>
                  <span className="text-[9px] text-gray-600 bg-gray-900 px-2 py-0.5 rounded">{flight.id.slice(-6)}</span>
                </div>
                
                <div className="flex gap-2">
                  <button onClick={() => focusFlight(flight.id)} className="flex-1 bg-gray-800 hover:bg-gray-700 py-2 rounded text-[10px] font-bold text-white uppercase tracking-tighter transition">Focus</button>
                  <button onClick={() => replayFlight(flight.id)} className="flex-1 border border-blue-500/30 text-blue-500 hover:bg-blue-500/10 py-2 rounded text-[10px] font-bold uppercase transition">
                    {playing === flight.id ? "Replaying" : "Replay"}
                  </button>
                </div>
              </div>
            ))
          )}
        </div>

        {/* Global Map View */}
        <div id="adminMap" className="flex-1 h-full bg-[#0b0e14]" />
      </div>
    </div>
  );
}